import axios from '@/libs/api.request'

// ktv
export const getOrderList = (params) => {
	return axios.request({
	  url: `/order/info`,
	  params,
	  method: 'get'
	})
}

// 
export const putOrderDeliverStatues = (data) => {
	let id = data.id;
	delete data.id;
	return axios.request({
	  url: `/order/package/info/${id}`,
	  data,
	  method: 'patch'
	})
}

// 
export const getOrderDetail = (id) => {
	return axios.request({
	  url: `/order/info/${id}`,
	  method: 'get'
	})
}