import {
  socketInfomation
}
from "@/api/information";
import {
  wsk
} from "@/libs/api.request.js";
import Centrifuge from "centrifuge";
import {
  getToken,
} from '@/libs/util'

class WebSocket extends Centrifuge {

  constructor(data) {
    super(data);
    this.subscribeKeys = [];
    this.init();
  }

  init() {
    window.onbeforeunload = () => {
      this.disconnect();
    };

    this.on("connect", context => {
      console.log("", context);
    });

    this.on("disconnect", context => {
      console.log("", context);
    });

    this.on("error", error => {
      console.log("", error);
    });

    this.connect();
  }

  addListeners(key, callback) {
    if (!this.hasKey(key)) {
      this.subscribeKeys.push(key);
    }
    this.subscribe(key, callback);
  }

  removeListeners(key, callback) {
    if (!this.hasKey(key)) return;
    this.unsubscribe(key);
    this.subscribeKeys.splice(this.subscribeKeys.indexOf(key), 1);
  }

  hasKey(key) {
    return this.subscribeKeys.indexOf(key) > -1 ? true : false;
  }

}

let WEBSOCKET = null;

export const websocket = () => {
  return new Promise((resolve, reject) => {
    if (WEBSOCKET == null) {
      socketInfomation().then(res => {

        const sign = res.data.data.access_token; //  secretusertimestamphashmapsha256
        const timestamp = res.data.data.timestamp; // 
        const user = res.data.data.user; // id
        WEBSOCKET = new WebSocket({
          url: wsk,
          user: user,
          timestamp: timestamp,
          token: sign
        })
        resolve(WEBSOCKET);
      })
    } else {
      if (WEBSOCKET._status == 'disconnected') {
        WEBSOCKET.connect();
      }
      resolve(WEBSOCKET);
    }
  })
}
